<template>
    <div class="container">
        <form>
            <div class="row">
                <div class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3">
                    <h1>Input</h1>
                    <hr>
                    <!--                    Sign in-->
                    <div v-if="!isTokenSet">
                        <div class="form-group">
                            <label for="email">Mail</label>
                            <input
                                    type="text"
                                    id="email"
                                    class="form-control"
                                    required
                                    v-model="user.mail">
                            <small style="color: red"
                                   v-if="user.mail.length===0">Please fill up email field</small>
                        </div>
                        <div class="form-group">
                            <label for="password">Password</label>
                            <input
                                    type="password"
                                    id="password"
                                    class="form-control"
                                    required
                                    v-model="user.password">
                            <small style="color: red"
                                   v-if="user.password.length===0">Please fill up password field</small>
                        </div>
                        <button
                                class="btn btn-primary"
                                @click.prevent="loginUser">Submit!
                        </button>
                    </div>

                    <!--                    Taking input-->
                    <hr>

                    <div v-if="isTokenSet">

                        <!--                        Add chapter-->
                        <div class="form-group">
                            <div class="form-group">
                                <label for="addChapter">Add Chapter</label>
                                <input
                                        type="text"
                                        class="form-control"
                                        id="addChapter"
                                        required
                                        v-model="newChapter">
                            </div>
                            <button
                                    class="btn btn-primary"
                                    @click.prevent="addChapter">Add Chapter
                            </button>
                        </div>

                        <hr>

                        <!--                        Add TUtorial-->
                        <h3>Add Tutorial</h3>
                        <div class="form-group">
                            <label for="selectChapter">Select Chapter</label>
                            <select
                                    id="selectChapter"
                                    class="form-control"
                                    v-model="selectedChapter">
                                <option v-for="chapter in chapters">{{chapter.name}}</option>
                            </select>
                            <div class="form-group">
                                <label for="videoTitle">Video Title: </label>
                                <input
                                        type="text"
                                        id="videoTitle"
                                        class="form-control"
                                        required
                                        v-model="tutorial.videoTitle">
                            </div>
                            <div class="form-group">
                                <label for="videoLink">Video Link: </label>
                                <input
                                        type="text"
                                        id="videoLink"
                                        class="form-control"
                                        required
                                        v-model="tutorial.videoLink">
                            </div>
                            <div class="form-group">
                                <label for="downloadLink">Video Download Link: </label>
                                <input
                                        type="text"
                                        id="downloadLink"
                                        class="form-control"
                                        required
                                        v-model="tutorial.downloadLink">
                            </div>
                            <div class="form-group">
                                <label>New Date: </label>
                                <v-date-picker v-model="tutorial.remainNewDate"/>
                              </div>
                            <button
                                    class="btn btn-primary"
                                    @click.prevent="addTutorial">Add Tutorial
                            </button>
                            <br>
                        </div>

                        <hr>
                        <!--                        Add YEar-->
                        <div class="form-group">
                            <div class="form-group">
                                <label for="addYear">Add Year</label>
                                <input
                                        type="text"
                                        class="form-control"
                                        id="addYear"
                                        required
                                        v-model="newYear">
                            </div>
                            <button
                                    class="btn btn-primary"
                                    @click.prevent="addYear">Add Year
                            </button>
                        </div>

                        <hr>

                        <!--                        Add Result-->
                        <h3>Add Tutorial</h3>
                        <div class="form-group">
                            <label for="selectYear">Select Year</label>
                            <select
                                    id="selectYear"
                                    class="form-control"
                                    v-model="selectedYear">
                                <option v-for="year in years">{{year}}</option>
                            </select>

                            <div class="form-group">
                                <label for="resultTitle">Result Title: </label>
                                <input
                                        type="text"
                                        id="resultTitle"
                                        class="form-control"
                                        required
                                        v-model="result.resultTitle">
                            </div>
                            <div class="form-group">
                                <label for="downloadResultLink">Result Download Link: </label>
                                <input
                                        type="text"
                                        id="downloadResultLink"
                                        class="form-control"
                                        required
                                        v-model="result.downloadLink">
                            </div>
                            <div class="form-group">
                                <label>New Date: </label>
                                <v-date-picker v-model="result.remainNewDate"/>
                            </div>
                            <button
                                    class="btn btn-primary"
                                    @click.prevent="addResult">Add Result
                            </button>
                            <br>
                        </div>

                        <hr>
                    </div>
                </div>
            </div>

        </form>
    </div>
</template>

<script>
    import axios from 'axios';

    const baseURL='https://spam-tutorial-result.firebaseio.com/';

    export default {
        data(){
            return {
                user: {
                    mail: '',
                    password: ''
                },
                //tokenId: 'eyJhbGciOiJSUzI1NiIsImtpZCI6IjFmODhiODE0MjljYzQ1MWEzMzVjMmY1Y2RiM2RmYjM0ZWIzYmJjN2YiLCJ0eXAiOiJKV1QifQ..pw1xmvG7mkDpw4OKWXf6RFuIsIpJAPkwQWiYqtNmzX98jclQ0a04gkRuV_X4EtHfpf8jfZ5ta2az4vht_I3g9Zb3R6gwuKSCeHZJHaH3ogMxcqZ65UDhJwLqIkbAOR92ZU7vgP7sJHS4aqo893mzrKCBv4FTKJP7eXrjBDmXJ6YfWAoNlUqtlEr51WwRuzWcYcHcYU-EHfYtSZzjJNkI0ck3ae2Q5NB8ClT6uUbYMVFvkX1X5k7P3Nan3regIkvqimuxOm45SeOe-rB4SYd7im-3S0GBMwHZIGkQjKD1ZouLEw7pR1v_v8wGZGBhl2FTjS9-PKlNpG_rykesVvL3ew',
                tokenId: '',
                newChapter: '',
                chapters:[],
                tutorial: {
                    videoTitle: '',
                    videoLink: '',
                    downloadLink: '',
                    remainNewDate: new Date()
                },
                selectedChapter: '',
                newYear: 20,
                years: [],
                result: {
                    resultTitle: '',
                    downloadLink: '',
                    remainNewDate: new Date()
                },
                selectedYear: ''
            }
        },
        computed:{
            isTokenSet(){
                return this.tokenId.length>0;
            }
        },
        methods: {
            loginUser(){
                if(this.user.mail.length===0 || this.user.password.length===0){
                    return;
                }
                const data={
                    email: this.user.mail,
                    password: this.user.password,
                    returnSecureToken: true
                };

                axios.post('https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyAp-RsnLgEuZ9SB8Sv1dEk3ne6WWHpmJGU ',
                    data).then(res=>{
                    ////console.log(res);
                    this.initForm(res);

                }).catch(error=> {
                    ////console.log(error);
                });
            },

            signUpUser(){
                if(this.user.mail.length===0 || this.user.password.length===0){
                    return;
                }
                const data={
                    email: this.user.mail,
                    password: this.user.password,
                    returnSecureToken: true
                };

                ////console.log(JSON.stringify(data));

                axios.post('https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyAp-RsnLgEuZ9SB8Sv1dEk3ne6WWHpmJGU ',
                    data).then(res=>{
                    ////console.log(res);
                    this.initForm(res);
                }).catch(error=> {
                    //console.log(error);
                });
            },

            initForm(res){
                this.tokenId=res.data.idToken;

                const time=res.data.expiresIn;

                this.getAllChapter();
                this.getAllYear();

                setTimeout(()=>{
                    this.tokenId='';
                },time*1000);
            },

            addChapter(){
                if(this.newChapter.length===0)
                    return;
                if(this.tokenId.length===0)
                    return;

                if(this.isUniqueChapter()===false){
                    alert("This chapter already exist.Chapter must be unique");
                    return;
                }

                //const link=baseURL+"chapters.json";
                const link=baseURL+"chapters.json?auth="+this.tokenId;
                ////console.log(link);

                axios.post(link,{chapter: this.newChapter})
                    .then(res=>{
                        alert('Chapter added successfully');
                        ////console.log(res);
                        this.getAllChapter();
                    }).catch(error=>{
                    alert('Chapter not saved');
                    ////console.log(error);
                });
            },

            isUniqueChapter(){
                for(let i=0;i<this.chapters.length;i++){
                    if(this.chapters[i].name===this.newChapter){
                        return false;
                    }
                }

                return true;
            },

            addTutorial(){
                if(this.selectedChapter.length===0)
                    return;

                if(this.tutorial.videoTitle.length===0)
                    return;

                if(this.tutorial.videoLink.length===0)
                    return;

                if(this.tutorial.downloadLink.length===0)
                    return;

                if(this.tokenId.length===0)
                    return;

                //const link=baseURL+"chapters.json";
                const link=baseURL+this.getChapterKey()+".json?auth="+this.tokenId;
                //console.log(link);

                const ye = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(this.tutorial.remainNewDate);
                const mo = new Intl.DateTimeFormat('en', { month: '2-digit' }).format(this.tutorial.remainNewDate);
                const da = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(this.tutorial.remainNewDate);

                const alertDate=`${mo}/${da}/${ye}`;
                //console.log(alertDate);

                const newTutorial={
                    videoTitle: this.tutorial.videoTitle,
                    videoLink: this.tutorial.videoLink,
                    downloadLink: this.tutorial.downloadLink,
                    alertDate
                };

                //console.log(newTutorial);

                axios.post(link,newTutorial)
                    .then(res=>{
                        alert('Tutorial added successfully');
                        //console.log(res);
                        //this.getAllChapter();
                    }).catch(error=>{
                        alert('Tutorial not saved');
                        //console.log(error);
                });
            },

            addYear(){
                if((this.newYear+'').length!==2){
                    alert("Year must be of 2 digits and a number.");
                    return;
                }
                if(this.isUniqueYear()===false){
                    alert("This year already exist.Year must be unique");
                    return;
                }
                if(this.tokenId.length===0)
                    return;

                //const link=baseURL+"chapters.json";
                const link=baseURL+"years.json?auth="+this.tokenId;
                ////console.log(link);

                axios.post(link,{year: this.newYear})
                    .then(res=>{
                        alert('Year added successfully');
                        ////console.log(res);
                        this.getAllYear();
                    }).catch(error=>{
                    alert('Year not saved');
                    ////console.log(error);
                });
            },

            isUniqueYear(){
                for(let i=0;i<this.years.length;i++){
                    if(this.years[i]==this.newYear){
                        return false;
                    }
                }

                return true;
            },

            addResult(){
                if(this.selectedYear.length===0)
                    return;

                if(this.result.resultTitle.length===0)
                    return;

                if(this.result.downloadLink.length===0)
                    return;

                if(this.tokenId.length===0)
                    return;

                //const link=baseURL+"chapters.json";
                const link=baseURL+this.selectedYear+".json?auth="+this.tokenId;
                ////console.log(link);

                const ye = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(this.result.remainNewDate);
                const mo = new Intl.DateTimeFormat('en', { month: '2-digit' }).format(this.result.remainNewDate);
                const da = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(this.result.remainNewDate);

                const alertDate=`${mo}/${da}/${ye}`;
                ////console.log(alertDate);

                const newResult={
                    resultTitle: this.result.resultTitle,
                    downloadLink: this.result.downloadLink,
                    alertDate
                };

                ////console.log(newResult);

                axios.post(link,newResult)
                    .then(res=>{
                        alert('Result added successfully');
                        ////console.log(res);
                    }).catch(error=>{
                    alert('Result not saved');
                    ////console.log(error);
                });
            },

            getAllChapter(){
                this.chapters=[];
                const link=baseURL+"chapters.json";
                axios.get(link)
                    .then(res=>{
                        for(let key in res.data){
                            let chap={
                                name: res.data[key].chapter,
                                key: key
                            };
                            this.chapters.push(chap);
                        }
                    }).catch(error=>{
                    ////console.log(error);
                });
            },

            getChapterKey(){
                for(let i=0;i<this.chapters.length;i++){
                    if(this.chapters[i].name===this.selectedChapter){
                        return this.chapters[i].key;
                    }
                }

                return null;
            },

            getAllYear(){
                this.years=[];
                const link=baseURL+"years.json";
                axios.get(link)
                    .then(res=>{
                        for(let key in res.data){
                            this.years.push(res.data[key].year);
                        }
                    }).catch(error=>{
                    ////console.log(error);
                });
            }
        }
    }
</script>

